                            </div>
                            <div class="col-md-12">
                                <log-componente subject_type="App\Models\Financeiro\Conta" :subject_id="itemModal.conta_id" :index="itemModal.conta_id"></log-componente>
                            </div>
                    </div>
                </div>
                 <div class="modal-body text-center">
                    <mensagem-componente v-if="erroModal" :erros="erroModal" @fechar="erroModal=null"></mensagem-componente>
                    <mensagem-componente v-if="sucessoModal" :sucesso="sucessoModal" @fechar="sucessoModal=null"></mensagem-componente>
                </div>
               <div class="modal-footer" v-if="itemModal.acao == 'cancelar'">
                    <div class="form-control text-right">
                        <label for="confirmar_taxas" class="control-label">Quero Cancelar esta conta</label>
                        <input type="checkbox" name="confirmar_taxas" v-model="itemModal.queroCancelar" id="confirmar_taxas" class="">                        
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">Fechar</button>
                    <button type="button" class="btn btn-primary" v-if="itemModal.acao == 'baixar'" :disabled="!caixaBaixa || (caixaBaixa.conta_bancaria && !forma_desc) ||  somaParcelasSelecionadas <= 0"
                            @click="baixarParcelas()">
                        <i class="fa fa-save"></i> Baixar Parcelas
                    </button>
                    <button type="button" class="btn btn-danger" v-if="itemModal.acao == 'cancelar'" :disabled="!itemModal || !itemModal.queroCancelar"
                        @click="cancelarConta()">
                        <i class="fa fa-close"></i> Cancelar
                    </button>
                </div>
            </div>
        </div>
    </div>

    <div class="modal fade" id="modal-relatorio-contas" tabindex="-1" aria-labelledby="modal-relatorio-contas-title" aria-hidden="true" style="display: none;">
        <div class="modal-dialog modal-dialog-scrollable">
            <div class="modal-content" style="min-height: 450px" >
                <div class="modal-header">
                    <h4 class="modal-title mt-0" id="modal-relatorio-contasTitle"> Relatório de Contas</h4>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">×</span>
                    </button>
                </div>
                <div class="modal-body" >
                    <RelatorioContas tipo="RECEBER"></RelatorioContas>
                </div>
            </div>
        </div>
    </div>

</div>

    
</template>

<script>
import { CaixaService,  CentroCustoService, ContaReceberService, ParceiroService, PlanoContasService } from '../../../services'
import { required, maxValue, minValue, requiredIf } from "vuelidate/lib/validators";
import RelatorioContas from '../../../components/financeiro/RelatorioContas'
import moment from 'moment';
const caixaService = new CaixaService();
const planoContaService = new PlanoContasService();
const parceiroService = new ParceiroService();
const centroCustoService = new CentroCustoService();
const contasReceberService = new ContaReceberService();
export default {
    components:{
        RelatorioContas
    },
    props:{
        rota : {
            type : String,
            default: ''
        }
    },
    data(){
        return{
            dados: [],
            
            caixas: [],            
            
            contas: [],
            parceiros:[],
            centros_custo: [],
            meusCaixas:[],
            caixaBaixa: null,
            forma_desc: '',
            data_lancamento: null,
            somaParcelasSelecionadas: 0,
            filtro:{
                ordenar: 'data_vencimento',
                ordem: 'asc',
                page: 1,
                per_page: 50,
                texto: '',
                status: 'PENDENTE',
                classificacao: 'TODAS',
                parceiro_id: null,
                data_final: null,
                data_inicial : null,
                data_referencia: 'VENCIMENTO'
            },
            filtro_referencias: ['VENCIMENTO','LANCAMENTO','EMISSAO', 'PAGAMENTO'],
            filtro_parceiro: null,
            paginacao: {
                current_page: 0,
                total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 50
            },
            debounce: null,
            itemModal: null,
            sucessoModal: null,
            erroModal: null,
            contaModal: {
                id: null,
                tipo: 'ENTRADA'
            },
            lctoModal: null,
            sucessoEditar: null,
            erroEditar: null,
            baixar_conjunto: false,
            erroBaixarConjunto : null,
            sucessoBaixarConjunto : null,

            filtro_totalizadores: {
                periodo: false,
                classificacao: 'TODAS',
                tipo: 'RECEBER',
                parceiro_id: null
            },
            totalizadores: {
                vencidas:0,
                vencidas_geral:0,
                dias_10:0,
                dias_10_geral:0,
                periodo:0,
                periodo_geral:0,
                total:0,
                total_geral:0,
            },
            buscandoTotalizadores: true
        }
    },
    validations() {
        return {
            itemModal: {
                conta:{
                    parcelas: {
                        required,
                        $each: {
                            valor_baixar: {required: requiredIf(function(x) {return !x.pagar})},
                        }
                    }

                }
                
            },
        }
    },
    methods:{
        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },        
        
        buscar:function(){
            this.dados = [];
            let obj = {...this.filtro};
            if(this.filtro_parceiro)
                obj.parceiro_id = this.filtro_parceiro.id
            if(obj.data_inicial)
                obj.data_inicial = moment(obj.data_inicial).format('YYYY-MM-DD');
            if(obj.data_final)
                obj.data_final = moment(obj.data_final).format('YYYY-MM-DD');
            contasReceberService.listar(obj).then(
                response => {
                    this.dados = response.data.data.map(x => {
                        x.saldo = (x.valor *100 - x.valor_pago * 100) / 100
                        return x
                    });
                    this.paginacao = response.data;
                    this.filtro.page = response.data.current_page;
                    this.paginacao.per_page = response.data.per_page;
                }
            )
        },

        buscarTotalizadores(forcar = false){
            
            let periodo = this.filtro.data_inicial && this.filtro.data_final
           
            if( !forcar &&
                this.totalizadores && 
                this.filtro_totalizadores.periodo == periodo &&
                this.filtro_totalizadores.classificacao == this.filtro.classificacao &&
                this.filtro_totalizadores.parceiro_id == this.filtro.parceiro_id
            )
                return;
            
            this.buscandoTotalizadores = true;

            this.filtro_totalizadores = {
                 periodo: periodo,
                classificacao: this.filtro.classificacao,
                parceiro_id: this.filtro.parceiro_id
            }

            let obj = {
                tipo: 'RECEBER',
                classificacao: this.filtro_totalizadores.classificacao,
                parceiro_id: this.filtro.parceiro_id
            }
            if(periodo){
                obj.data_inicial = moment(this.filtro.data_inicial).format('YYYY-MM-DD')
                obj.data_final = moment(this.filtro.data_final).format('YYYY-MM-DD')
            }

            contasReceberService.buscarTotalizadores(obj).then( response => this.totalizadores = response.data).finally( () => this.buscandoTotalizadores = false)

        },
        
        buscarContaDespesaDebounce: function(text, loading){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.buscarContaDespesa(text, loading)
            }, 800);
            
        },
        buscarContaDespesa: function(text, loading){
            let filtro = {tipo: "SAIDA", texto: text, per_page:40};
            planoContaService.listar(filtro).then(
                response => this.contas = response.data.data.map(x => {
                    x.nome = x.codigo + ' - ' + x.nome;
                    return x;
                })
            );
        },
        buscarParceiroDebounce: function(text, loading){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.buscarParceiro(text, loading)
            }, 800);
            
        },
        buscarParceiro: function(text, loading){
            let filtro = {texto: text, per_page:40};
            parceiroService.listar(filtro).then( response => this.parceiros = response.data.data)
        },
        novaConta: function(){
            if(this.contas.length == 0)
                this.buscarContaDespesa('','');
            if(this.caixas.length == 0)
                this.buscarCaixas();
            this.$v.contaModal.$reset();
            this.contaModal = {
                data_competencia: this.contaModal.data_competencia ? this.contaModal.data_competencia : new Date(),
                qtde_parcelas: 1,
                com_entrada: true,
                parcelas: [{}],
            };
      
           setTimeout(() => {
                this.$v.contaModal.$reset();
                this.sucessoEditar = null;
                this.erroEditar = null;
           }, 10);
        },
        autoGerarParcelas: function(){
            if(this.contaModal.valor_total > 0 && !this.contaModal.parcelas_gerada){
                this.gerarParcelas(true);
            }
        },
        gerarParcelas: function( auto = false){
                     
            if(!(this.contaModal.valor_total > 0)){
                this.$v.contaModal.valor_total.$touch();
                Vue.$toast.error('Preencha o Valor para gerar as parcelas')
                return false;
            }
            if(!this.contaModal.qtde_parcelas || !(this.contaModal.qtde_parcelas > 0)){
                Vue.$toast.error('Preencha a quantidade de parcelas para gerar!')
                return false;
            }
            let decimais = 1;
            for(let i = 0; i < 2; i++){
                decimais = decimais * 10;
            }
            let valor = Math.round((this.contaModal.valor_total / this.contaModal.qtde_parcelas) * decimais) / decimais;
            this.contaModal.parcelas = [];
            let vencimento = this.contaModal.com_entrada ? moment() : moment().add('1', 'months');
            if(this.contaModal.com_entrada){
                this.contaModal.parcelas.push(
                    {
                        parcela: 0,
                        valor: valor,
                        data_vencimento: vencimento.toDate(),
                        
                    }
                )
                vencimento = vencimento.add('1', 'months')
            }
            
            for (let i = 0; i < this.contaModal.qtde_parcelas; i++){
                this.contaModal.parcelas.push(
                    {
                        parcela: i+1,
                        valor: valor,
                        data_vencimento: vencimento.toDate(),
                        
                    }
                )
                vencimento = vencimento.add('1', 'months')
            };
            let sobra = this.contaModal.valor_total - (valor * this.contaModal.qtde_parcelas);
            this.contaModal.parcelas[this.contaModal.qtde_parcelas - 1  ].valor = Math.round((this.contaModal.parcelas[this.contaModal.qtde_parcelas - 1  ].valor + sobra) * decimais) / decimais;
            if(!auto)
                this.contaModal.parcelas_gerada = true;
        },
        removerParcela: function(index){
            this.contaModal.parcelas.slice(index,1);
        },
        adicionarParcela: function(){
            this.contaModal.parcelas.push({});
        },
        abrirModalVerLancamento:function(obj){            
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.lctoModal = {...obj};
        },
        salvarConta: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.contaModal.parcelas.map((x,i) => {
                x.parcela = i+1,
                x.parcelas = this.contaModal.parcelas.length
                return x;
            })
            this.$v.contaModal.$touch();
            if(this.saldoParcelas != 0 || this.$v.contaModal.parcelas.$invalid){
                this.erroEditar = "Revise as parcelas, a soma das parcelas deve ser exatamente o mesmo valor total da conta, e todas devem estar com a data de vencimento preenchidas!"
                return 0;
            }
            if(this.$v.contaModal.$invalid){
                this.erroEditar = this.$v.contaModal
                return 0;
            }
            
            
        contasReceberService.criar({...this.contaModal})
            .then( response => {
                this.sucessoEditar = "Conta Criada com Sucesso!"
                this.buscar();
            }).catch( erro => this.erroEditar = erro)            
        },
        excluirLancamento:function(){
            this.sucessoEditar = null;
            this.erroEditar = null;
                        
            caixaService.excluirLancamentoManual(this.caixa.id, this.lctoModal)
            .then( response => {
                this.sucessoEditar = "Lançamento Excluido com Sucesso!"
                this.buscar();
            }).catch( erro => this.erroEditar = erro)
        },
        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },
        buscarCaixas: function(){
            let filtro = {tipo: "TRANSFERENCIA", texto: '', per_page:40};
            planoContaService.listar(filtro).then(
                response => this.caixas = response.data.data.map(x => {
                    x.nome = x.codigo + ' - ' + x.nome;
                    return x;
                })
            );
        },
        buscarMeusCaixas: function(){
            
            caixaService.meusCaixas().then(
                response => this.meusCaixas = response.data
            );
        },
        
        buscarCentrosCusto: function(){
            centroCustoService.listar({per_page:1000, ativo:1}).then( response => this.centros_custo = response.data.data.map(x => { return {nome: x.codigo + ' | ' + x.nome, id: x.id} }) );
        },

        
        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },
        desabilitaDataFuturaPeriodoInicial: function(data) {
            if(!this.filtro.data_final)
                return false;
            return this.filtro.data_final <= data
        },
        desabilitaDataPassadaPeriodoFinal: function(data) {
            if(!this.filtro.data_inicial)
                return false;
            return this.filtro.data_inicial >= data
        },
        selecionarTexto: function(event){
            event.target.setSelectionRange(0, texto.length);
        },
        abrirModalItem: function(item, acao){
            
            this.erroModal = null;
            this.sucessoModal = null;
            this.data_lancamento = new Date();
            this.itemModal = null;
            this.itemModal = {...item, acao: acao};
            this.itemModal.carregouParcelas = false;
            this.buscarConta();
            // if(acao == 'baixar' && this.meusCaixas.length == 0){
            //     this.buscarMeusCaixas();
            // }
            this.soma_parcelas_selecionadas_baixa();
        },
        buscarConta:function(){
            
            contasReceberService.buscar(this.itemModal.conta_id)
                .then(response => {
                    this.itemModal.conta.parcelas = response.data.parcelas.map(x => {
                        x.pagar = x.id == this.itemModal.id && !x.valor_pago;
                        
                        x.valor = Number(x.valor)
                        x.valor_pago = Number(x.valor_pago)
                        x.valor_taxa_financeira = Number(x.valor_taxa_financeira)
                        x.saldo = (x.valor *100 - x.valor_pago * 100) / 100
                        x.valor_baixar = x.saldo
 
                        return x
                    });
                    this.itemModal.caixa_baixa_nota_entrada = response.data.caixa_baixa_nota_entrada;

                    if(this.itemModal.caixa_baixa_nota_entrada){
                        this.caixaBaixa = {...this.itemModal.caixa_baixa_nota_entrada}
                    }
                    this.itemModal.carregouParcelas = true;
                    this.soma_parcelas_selecionadas_baixa();
                    this.$forceUpdate();
                } );
        },
        baixarParcelas: function(){
            this.erroModal = null;
            this.sucessoModal = null;

            if(this.itemModal.conta.parcelas.some(x => x.pagar && (x.valor_baixar > x.saldo ||  x.valor_baixar <= 0))){
                this.erroModal = 'Não é possível baixar valores zerados ou acima do saldo da parcela'
                return 0
            }

            let parcelasBaixar = {
                parcelas: this.itemModal.conta.parcelas.filter(x => x.pagar).map(x => {
                    return { 
                        id: x.id, parcela: 
                        x.parcela, 
                        valor: x.valor, 
                        valor_baixar: x.valor_baixar, 
                        data: moment().format('YYYY-MM-DD') 
                    }
                }), 
                data_lancamento: moment(this.data_lancamento).format('YYYY-MM-DD'),
                forma_desc: this.caixaBaixa.conta_bancaria ? this.forma_desc : 'DINHEIRO'
            };
            caixaService.baixarParcelasReceber(this.caixaBaixa.id, parcelasBaixar)
                .then(response => {
                    this.itemModal.conta.parcelas = this.itemModal.conta.parcelas.map(x => {
                        x.pagar = false;
                        return x;
                    });
                    setTimeout(() => {
                        this.buscarConta();
                    }, 20);
                    this.soma_parcelas_selecionadas_baixa();
                    this.sucessoModal = "Parcelas Baixadas com Sucesso!";
                    this.buscar();
                    this.buscarTotalizadores(true);

                }).catch( error => {
                    this.erroModal = error;
                })
        },
        cancelarBaixarConjunto: function(){
            this.erroBaixarConjunto = null;
            this.sucessoBaixarConjunto = null;
            this.dados = this.dados.map(x => { x.baixar = false; return x});
            this.caixaBaixa = null;
            this.baixar_conjunto = false;
        },

        baixarConjunto: function(){
            this.erroBaixarConjunto = null;
            this.sucessoBaixarConjunto = null;
            let parcelas = { parcelas: this.baixar_conjunto_parcelas.map(x => x.id)}; // ids das parcelas
            caixaService.baixarParcelasConjunto(this.caixaBaixa.id, parcelas)
                .then(response => {
                    this.dados = this.dados.map(x => {
                        
                        if(parcelas.parcelas.indexOf(x.id) >= 0){
                            x.quitado = 1;
                        }
                        x.baixar = false;
                        return x;
                    })
                    this.sucessoBaixarConjunto = "Parcelas Baixadas com Sucesso!";
                    this.buscar();
                    this.buscarTotalizadores(true);
                }).catch( error => {
                    this.erroBaixarConjunto = error;
                })
        },
        soma_parcelas_selecionadas_baixa() {            
            if(!this.itemModal || !this.itemModal.conta || !this.itemModal.conta.parcelas){
                this.somaParcelasSelecionadas =  0;
                return 0;
            }                
            this.somaParcelasSelecionadas = this.itemModal.conta.parcelas.reduce( (ac,atual) => { 
                return ac + (atual.pagar ? atual.valor_baixar : 0)
             }, 0);
        },
        cancelarConta: function(){
            this.erroModal = null;
            this.sucessoModal = null;
            
            contasReceberService.cancelar( this.itemModal.conta.id)
                .then(response => {
                    this.itemModal.conta.status = 'CANCELADO';
                    this.sucessoModal = "Conta Cancelada com Sucesso!";
                    this.buscar();
                    this.buscarTotalizadores(true);
                }).catch( error => {
                    this.erroModal = error;
                })  
        },
        imprimir: function(parcelaId, tipo){
            let url = contasReceberService.imprimir(parcelaId)+'?tipo_impressao='+tipo;
            window.open(url,'_blank');
        },
        imprimir_listagem: function(estilo = null){
            let obj = {...this.filtro, estilo: estilo};
            if(obj.data_inicial)
                obj.data_inicial = moment(obj.data_inicial).format('YYYY-MM-DD');
            if(obj.data_final)
                obj.data_final = moment(obj.data_final).format('YYYY-MM-DD');
            let url = contasReceberService.urlListagem(obj);
            window.open(url,'_blank');
        },

        imprimir_agrupado: function(){
            let obj = {...this.filtro};
            if(obj.data_inicial)
                obj.data_inicial = moment(obj.data_inicial).format('YYYY-MM-DD');
            if(obj.data_final)
                obj.data_final = moment(obj.data_final).format('YYYY-MM-DD');
            let url = contasReceberService.urlLAgrupado(obj);
            window.open(url,'_blank');
        },
        
    },
    watch: {
        
        'filtro.ordenar': function (val) {
            this.buscar()
        }, 
        'filtro.ordem': function (val) {
            this.buscar()
        }, 
        'filtro.page': function (val) {
            this.buscar()
        }, 
        'filtro.per_page': function (val) {
            this.buscar()
        }, 
        'filtro.status': function (val) {
            this.debounceBusca()
        }, 
        'filtro.classificacao': function (val) {
            this.debounceBusca()
            this.buscarTotalizadores()
        }, 
        'filtro.texto': function (val) {
            this.debounceBusca()
        }, 
          'filtro.data_final': function (val) {
            this.debounceBusca()
            this.buscarTotalizadores()
        }, 
        'filtro.data_inicial': function (val) {
            this.debounceBusca()
            this.buscarTotalizadores()
        }, 
        'filtro.data_referencia': function (val) {
            this.navegar(1)
        }, 
     
    },
    mounted(){
        
        this.buscarTotalizadores();    
        setTimeout(() => {
            this.buscarMeusCaixas();
        }, 100);
        setTimeout(() => {
            this.buscar();
        }, 300);
        
        setTimeout(() => {
            this.buscarCaixas();
        }, 200);
        
        setTimeout(() => {
            this.buscarCentrosCusto();
        }, 1000);
    },
    computed: {
        money: function(){
            return {
                decimal: ',',
                thousands: '.',
                prefix: this.$store.state.moeda + ' ',
                suffix: '',
                precision: 2,
                masked: false
            }
        },
        saldoParcelas(){
            if(! (this.contaModal.valor_total > 0))
                return 0;
            
            let soma = this.contaModal.parcelas.reduce((a,c) => {
                return a + c.valor;
            },0);
            return this.contaModal.valor_total - soma;
        },
        baixar_conjunto_parcelas(){
            let d = this.dados.filter(x => x.baixar);
            if(d.length == 0)
                return false;
            return d
        },
        soma_parcelas_baixar(){
            if (!this.baixar_conjunto_parcelas)
                return 0;
            
            let x = this.baixar_conjunto_parcelas.reduce( (a,c) => a + Number(c.valor), 0);
            x = Math.round(x * 100) / 100;
            x = Number(x).toLocaleString("pt-BR", { maximumFractionDigits: 2, minimumFractionDigits: 2 });
            return `${this.$store.state.moeda} ${x}`;
        },
        
    }
}
</script>
<style scoped>
 
 

</style>