<template>

    <div class="row">

        <label class="col-sm-4 col-form-label" >Período</label>
        <div class="col-sm-8 mb-4">
            <date-picker v-model="relatorio.periodo" range :format="'DD/MM/YYYY'" range-separator=" até " :clearable="false" ></date-picker>
        </div>
        
        <label class="col-sm-4 col-form-label" >Data de Referência</label>
        <div class="col-sm-8">
            <div class="custom-control custom-switch mb-3">
                <label class="control-label mb-10" for="input-relatorio-referencia-1" style="cursor: pointer">
                <input type="radio" id="input-relatorio-referencia-1" name="relatorio_data_referencia" value="lancamento"   v-model="relatorio.data_referencia" class="mb-1"> Data de Lançamento
                </label>
                <br>
                <label class="control-label mb-10" for="input-relatorio-referencia-2" style="cursor: pointer">
                <input type="radio" id="input-relatorio-referencia-2" name="relatorio_data_referencia" value="emissao" v-model="relatorio.data_referencia" class="mb-1"> Data de Emissão
                </label>
                <br>
                <label class="control-label mb-10" for="input-relatorio-referencia-3" style="cursor: pointer">
                <input type="radio" id="input-relatorio-referencia-3" name="relatorio_data_referencia" value="pagamento" v-model="relatorio.data_referencia" class="mb-1"> Data de Pagamento
                </label>
            </div>
        </div>

        <label class="col-sm-4 col-form-label" >Quitadas</label>
        <div class="col-sm-8">
            <div class="custom-control custom-switch mb-3">
                <label class="control-label mb-10" for="input-relatorio-quitadas-1" style="cursor: pointer">
                <input type="radio" id="input-relatorio-quitadas-1" name="relatorio_quitadas" value="quitada"   v-model="relatorio.quitada" class="mb-1"> Somente Pagas
                </label>
                <br>
                <label class="control-label mb-10" for="input-relatorio-quitadas-2" style="cursor: pointer">
                <input type="radio" id="input-relatorio-quitadas-2" name="relatorio_quitadas" value="pendente" v-model="relatorio.quitada" class="mb-1"> Somente Pendentes
                </label>
                <br>
                <label class="control-label mb-10" for="input-relatorio-quitadas-3" style="cursor: pointer">
                <input type="radio" id="input-relatorio-quitadas-3" name="relatorio_quitadas" value="todos" v-model="relatorio.quitada" class="mb-1"> Todas
                </label>
            </div>
        </div>

        <label class="col-sm-4 col-form-label" >Estilo do Relatório</label>
        <div class="col-sm-8">
            <div class="custom-control custom-switch mb-3">
                <label class="control-label mb-10" for="input-relatorio-estilo-1" style="cursor: pointer">
                <input type="radio" id="input-relatorio-estilo-1" name="producao_diaria_estilo" value="resumido"   v-model="relatorio.estilo" class="mb-1"> Resumido
                </label>
                <br>
                <label class="control-label mb-10" for="input-relatorio-estilo-2" style="cursor: pointer">
                <input type="radio" id="input-relatorio-estilo-2" name="producao_diaria_estilo" value="detalhado" v-model="relatorio.estilo" class="mb-1"> Detalhado
                </label>
            </div>        
        </div>
        
        <div class="col-md-12 text-right">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>
            <button @click="imprimir()" type="button" class="btn btn-primary"> <i class="mdi mdi-printer font-size-18"></i> Imprimir</button>
        </div>        

    </div>
    
    

</template>

<script>
import { ContaPagarService, ContaReceberService } from '../../services';
const contaPagarService = new ContaPagarService()
const contaReceberService = new ContaReceberService()
import moment from 'moment'
export default {
    props: {
        tipo: {
            type: String,
            default: 'PAGAR',
        },
    },
    data() {
        return {
          relatorio:{
                periodo: [
                 moment().startOf('month').toDate(),
                 moment().endOf('month').toDate(), 
                ],
                estilo: 'detalhado',
                tipo: 'GERAL',
                data_referencia: 'lancamento',
                quitada: 'todos'
            },
        };
    },

    methods: {
        imprimir(){
            let relatorio = {...this.relatorio}
            relatorio.tipo = this.$props.tipo;
            delete relatorio.periodo
            relatorio.data_inicial = moment(this.relatorio.periodo[0]).format('DD-MM-YYYY')
            relatorio.data_final = moment(this.relatorio.periodo[1]).format('DD-MM-YYYY')

            let url  = contaPagarService.urlRelatorio(relatorio);
            window.open(url, '_blank')
        }
    }
};
</script>
